.dot-pulse {
    position: relative;
    left: -9999px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: var(--text);
    color: var(--text);
    -webkit-box-shadow: 9999px 0 0 -5px var(--text);
    box-shadow: 9999px 0 0 -5px var(--text);
    -webkit-animation: dotPulse 1.5s infinite linear;
    animation: dotPulse 1.5s infinite linear;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}

.dot-pulse::before,
.dot-pulse::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: var(--text);
    color: var(--text);
}

.dot-pulse::before {
    -webkit-box-shadow: 9984px 0 0 -5px var(--text);
    box-shadow: 9984px 0 0 -5px var(--text);
    -webkit-animation: dotPulseBefore 1.5s infinite linear;
    animation: dotPulseBefore 1.5s infinite linear;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}

.dot-pulse::after {
    -webkit-box-shadow: 10014px 0 0 -5px var(--text);
    box-shadow: 10014px 0 0 -5px var(--text);
    -webkit-animation: dotPulseAfter 1.5s infinite linear;
    animation: dotPulseAfter 1.5s infinite linear;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

@-webkit-keyframes dotPulseBefore {
    0% {
        -webkit-box-shadow: 9984px 0 0 -5px var(--text);
        box-shadow: 9984px 0 0 -5px var(--text);
    }
    30% {
        -webkit-box-shadow: 9984px 0 0 2px var(--text);
        box-shadow: 9984px 0 0 2px var(--text);
    }
    60%,
    100% {
        -webkit-box-shadow: 9984px 0 0 -5px var(--text);
        box-shadow: 9984px 0 0 -5px var(--text);
    }
}

@keyframes dotPulseBefore {
    0% {
        -webkit-box-shadow: 9984px 0 0 -5px var(--text);
